import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import { exist } from '../../utils';

const sectionOverlayAnimation = () => {
	gsap.registerPlugin(ScrollTrigger);

	const SELECTORS = {
		section: '.js-section-overlay',
		inner: '.js-section-overlay-inner',
	};

	const $section = document.querySelector(SELECTORS.section);
	const $inner = document.querySelector(SELECTORS.inner);

	if (!exist($section) || !exist($inner)) return;

	gsap.set($inner, { opacity: 1 });

	const tl = gsap.timeline({
		paused: true,
		defaults: {
			ease: 'none',
		},
		scrollTrigger: {
			trigger: $section,
			start: 'bottom bottom',
			end: 'bottom+=100% bottom',
			scrub: true,
			pin: true,
			pinType: 'fixed',
		},
	});

	ScrollTrigger.config({
		ignoreMobileResize: true,
	});

	tl.fromTo(
		$inner,
		{
			clipPath: 'inset(100% 0 0 0)',
		},
		{
			clipPath: 'inset(0% 0 0 0)',
		},
	);
};

export default sectionOverlayAnimation;
