import innovation from '../components/innovation';
import storyHero from '../components/story-hero';
import sectionOverlayAnimation from '../components/animation/section-overlay-animation';
import { humanityCreativity } from '../components/humanity-creativity';
import createObserver from '../components/create-observer';
// import { teamSlider } from '../components/team-slider';

const dynamicSectionsLoad = async () => {
	const team = await import('../components/executive-team');
	team.team();

	const teamSlider = await import('../components/team-slider');
	teamSlider.teamSlider();

	const contactSection = await import('../components/contact-section');
	contactSection.contactSection();
};

const ourStory = () => {
	sectionOverlayAnimation();
	innovation();
	storyHero();
	humanityCreativity();

	// teamSlider();

	const $observerSection = document.querySelector('.executive_team');

	createObserver(dynamicSectionsLoad, $observerSection);
};

export default ourStory;
